import React, {Component} from 'react';
import './ToDo.css';
import ToDoItem from './components/ToDoItem';
import Logo from './assets/logo.png';

class ToDo extends Component {
    constructor(props) {
        super(props);

        let initialList = [
            {
                name: 'Matte1',
                points: 100,
                grade: 'A',
                merit: 0
            },
            {
                name: 'Matte2',
                points: 100,
                grade: 'A',
                merit: 0,
            }
        ];
        if (window.localStorage.getItem('herp') !== null) {
            initialList = JSON.parse(window.localStorage.getItem('herp'))
            initialList.forEach(item => {
                if (!('merit' in item)) {
                    item.merit = 0
                }
            })
        }


        this.state = {
            // this is where the data goes
            list: initialList,
            todo: '',
        };
    };

    createNewToDoItem = () => {
        this.setState(({list, todo}) => ({
            list: [
                ...list,
                {
                    name: todo,
                    points: 100,
                    grade: 'A',
                    merit:0,
                }
            ],
            todo: ''
        }));
    };


    handleKeyPress = e => {
        if (e.target.value !== '') {
            if (e.key === 'Enter') {
                this.createNewToDoItem();
            }
        }
    };

    handleInput = e => {
        this.setState({
            todo: e.target.value
        });
    };


    // this is now being emitted back to the parent from the child component
    deleteItem = indexToDelete => {
        this.setState(({list}) => ({
            list: list.filter((toDo, index) => index !== indexToDelete)
        }));
    };

    changePoints(index, value) {
        this.setState(({list}) => {


            const newList = list.map((item, key) => {
                if (key === index) {
                    return Object.assign({}, item, {points: value})
                } else {
                    return item
                }

            });

            return {
                list: newList,
            }
        });
    }

    changeGrade(index, value) {
        this.setState(({list}) => {


            const newList = list.map((item, key) => {
                if (key === index) {
                    return Object.assign({}, item, {grade: value})
                } else {
                    return item
                }

            });

            return {
                list: newList,
            }
        });
    }
    changeMerit(index, value) {
        this.setState(({list}) => {


            const newList = list.map((item, key) => {
                if (key === index) {
                    return Object.assign({}, item, {merit: value})
                } else {
                    return item
                }

            });

            return {
                list: newList,
            }
        });
    }

    calculateScore() {
        

        const totalPoints = this.state.list.map(item => Number(item.points))
            .reduce((acc, val) => acc + val, 0);
        
        const merit = this.state.list.map(item => Number(item.merit))
            .reduce((acc, val) => acc + val, 0);

        const e = {
            A: 20,
            B: 17.5,
            C: 15,
            D: 12.5,
            E: 10,
            F: 0
        };

        const weightedPoints = this.state.list.map(item => Number(item.points) * e[item.grade])
            .reduce((acc, val) => acc + val, 0);


        return {total: totalPoints, score: (weightedPoints / totalPoints).toFixed(2),merit:merit}

    }

    render() {

        let total, score,merit;
        ({total, score,merit} = this.calculateScore());

        window.localStorage.setItem('herp', JSON.stringify(this.state.list));

        return (


            <div className="ToDo">
                <h1 className="ToDo-Header">Gustavs Awesome Gynasie-Simulator!!</h1>
                <h2>Po√§ng: {total}{'      '}Betyg: {Number(score)+Number(merit)}</h2>
                <div className="ToDo-Container">

                    <div className="ToDo-Content">

                        {this.state.list.map((item, key) => {
                                return <ToDoItem
                                    key={key}
                                    name={item.name}
                                    points={item.points}
                                    grade={item.grade}
                                    changeMerit={(event)=>this.changeMerit(key,event)}
                                    changePoints={(event) => this.changePoints(key, event)}
                                    changeGrade={(event) => this.changeGrade(key, event)}
                                    deleteItem={this.deleteItem.bind(this, key)}
                                />
                            }
                        )}
                    </div>

                    <div>
                        <input type="text" value={this.state.todo} onChange={this.handleInput}
                               onKeyPress={this.handleKeyPress}/>
                        <button className="ToDo-Add" onClick={this.createNewToDoItem}>+</button>
                    </div>

                </div>
            </div>
        );
    }
}

export default ToDo;
