import React, {Component} from 'react';
import './ToDoItem.css';

class ToDoItem extends Component {

    constructor(props) {
        super(props);

    }


    render() {
        return (
            <div className="ToDoItem">
                <p className="ToDoItem-Text">{this.props.name}</p>
                <input type="number" value={this.props.points}
                       onChange={event => this.props.changePoints(event.target.value)}/>
                <select name="" value={this.props.grade} onChange={event => this.props.changeGrade(event.target.value)}>
                    <option value="A">A</option>
                    <option value="B">B</option>
                    <option value="C">C</option>
                    <option value="D">D</option>
                    <option value="E">E</option>
                    <option value="F">F</option>
                </select>
                <select name="" value={this.props.merit} onChange={event => this.props.changeMerit(event.target.value)}>
                    <option value="0"></option>
                    <option value="0.5">+0.5</option>
                    <option value="1">+1</option>
                </select>
                <button className="ToDoItem-Delete"
                        onClick={this.props.deleteItem}>-
                </button>
            </div>
        );
    }
}

export default ToDoItem;
